html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}

.form-question .select-items .select-items__input .preview-image img{height:200px}.auth-main{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100vh;width:100vw;padding:0px 30px 50px 30px;max-width:450px}.auth-main *{font-family:"Stylish", sans-serif}.auth-main .login-title{margin-bottom:40px;font-weight:700;font-size:2em;font-style:italic;color:#222831}.auth-main .signin-form{display:flex;flex-direction:column;align-items:center;width:100%}.auth-main .signin-form .signin-form__email,.auth-main .signin-form .signin-form__password{margin-bottom:10px;width:100%}.auth-main .signin-form .signin-form__submit{font-size:1.2em;width:100%;background-color:#1976d1;color:white;margin-top:10px}.auth-main .social-login{display:flex;flex-direction:column;margin-top:20px;width:100%}.auth-main .social-login button{color:white;margin:5px 0px}.auth-main .social-login .social-login__google{background-color:#ea232c}.auth-main .social-login .social-login__github{background-color:#222831}.auth-main .toggle-account{display:inline-block;width:100%;margin:10px;color:#1976d1;text-align:right}.auth-main .toggle-account:hover{cursor:pointer}.header{position:fixed;top:0;display:flex;width:100%;justify-content:space-between;align-items:center;background-color:#eee;padding:15px 20px;margin-bottom:20px}.header .header__menu{display:flex}.header .header__menu .header__menu-item{margin-right:10px}.header .header__menu .header__menu-item a{padding:5px;text-decoration:none;color:#1976d1;cursor:pointer}.header .header__logout{width:80px;padding:5px;border:none;background-color:skyblue;color:white;border-radius:10px}.footer{margin:30px 0;padding-top:15px;border-top:3px solid rgba(0,0,0,0.1);width:100%;display:flex;flex-direction:column;align-items:center}.footer .footer__icon{color:rgba(0,0,0,0.2);font-size:30px;margin-bottom:10px}.footer .footer__text{color:rgba(0,0,0,0.2);font-weight:800;text-transform:uppercase}.question-card{display:flex;flex-direction:column;align-items:center;border-top:2px solid #0000002e;border-radius:2px;padding-top:20px;margin-top:20px}.question-card .question__title{font-size:1.1em;font-weight:700}.question-card .question__items{display:flex;align-items:center}.question-card .question__items *{margin-top:10px}.question-card .question__items .quest-item{position:relative}.question-card .question__items .quest-item:active img{opacity:1}.question-card .question__items .quest-item:active span{display:none}.question-card .question__items .quest-item .item-image{width:100%;opacity:0.3;cursor:pointer;z-index:-1}.question-card .question__items .quest-item span{width:100%;height:100%;display:block;position:absolute;top:0;text-align:center;font-weight:700;color:#485460}.question-card .question__items .quest-item .item-count{padding-top:20px;font-size:2em}.question-card .question__items .quest-item .item-desc{padding-top:60px}.question-card .question-edit{width:100%;display:flex;justify-content:flex-end;margin-top:10px}.question-card .question-edit .edit-buttons button{border:none;border-radius:10px;padding:5px 10px;background-color:#485460;color:white;margin-left:5px}*{box-sizing:border-box;font-family:"Source Sans Pro", sans-serif}body{background-color:white;display:flex;justify-content:center}.main{margin-top:60px;padding:20px 30px}#root{width:100%}.auth-main button,.auth-main .signin-form .signin-form__submit{border:none;border-radius:10px;padding:10px;width:100%;font-weight:400;font-size:1.1em}.auth-main input,.auth-main .signin-form .signin-form__email,.auth-main .signin-form .signin-form__password{border:1px solid #393e46;border-radius:5px;padding:10px;font-size:1em}.auth-main input:hover{border:1px solid #222831}.auth-main input:focus{outline:0;border:2px solid #00adb5}

